import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'
import {openSnackbar} from 'reducers/snackbar/action-creators'
import {getContent, filterContent, filterContentSuccess, deleteContent} from 'reducers/firestore/action-creators'
import {openForm} from 'reducers/content-form/action-creators'
import {connect} from 'react-redux'

import 'typeface-roboto' // AVISO! Adicionei essa biblioteca para fontes nos Headers!
import Paper from '@material-ui/core/Paper'
import {unstable_Box as Box} from '@material-ui/core/Box'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Grid from '@material-ui/core/Grid'
import FormGroup from '@material-ui/core/FormGroup'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Checkbox from '@material-ui/core/Checkbox'
import Radio from '@material-ui/core/Radio'
import SearchIcon from '@material-ui/icons/Search'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Fab from '@material-ui/core/Fab'
import Typography from '@material-ui/core/Typography'
import BackIcon from '@material-ui/icons/ArrowBack'

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import CurriculumData from '../CurriculumData'

import styles from './styles'
import {withStyles} from '@material-ui/styles'

import imgurUtils from 'utils/imgur'
import {compose, renderComponent} from 'recompose'

import {firebase} from '../../' // Importando firebase aqui!
import {RadioGroup} from '@material-ui/core';

class CurriculumPage extends React.Component {


    constructor(props) {
        super(props)

        const {
            openSnackbar,
            classes,
        } = props

        this.state = {
            openSnackbar,
            classes,
            rows: [],
            willShowData: true,

            // Atributos de estado relacionados ao formulrio
            gender: [],
            highestgrade: [],
            techcourse: [],
            profexperience: [],
            cid: ""
        }

    }

    /**
     * Mtodo para transformar os dados de entrada em um dicionrio.
     */
    addCellData = (name, gender, birthdate, highestgrade, techcourse, profexperience, cid,
                   address, city, phone, email, info) => {
        return {
            name, gender, birthdate, highestgrade, techcourse, profexperience, cid, address,
            city, phone, email, info
        }
    }

    /**
     * Mtodo para reiniciar todos os dados resultantes da busca.
     */
    resetData() {

        // Resetando a varivel auxiliar 'willShowData' para true,
        // ou seja, ser colocado em estado que receber informaes novos
        // do banco de dados.
        this.setState({willShowData: true})

        // Resetando todas as informaes pr-existentes de busca anteriores.
        this.state.rows.forEach(element => this.state.rows.pop(element))

        // Mostrando o formulrio de busca novamente!
        this.props.changeFormState()
    }

    /**
     * Mtodo para gerenciar os checkboxes relacionados a gnero.
     */
    handleGenderCheckbox = name => (event) => {

        if (event.target.checked) {
            this.state.gender.push(String(event.target.value).toLowerCase())
        } else {

            for (let i = 0; i < this.state.gender.length; i++) {
                if (this.state.gender[i] == (event.target.value).toLowerCase()) {
                    this.state.gender.splice(i, 1)
                    break
                }
            }

        }

    }

    /**
     * Mtodo para gerenciar os checkboxes relacionados a Maior Grau de Educao.
     */
    handleHighestGradeCheckbox = id => (event) => {

        if (event.target.checked) {
            this.state.highestgrade.push(id)
        } else {

            for (let i = 0; i < this.state.highestgrade.length; i++) {
                if (this.state.highestgrade[i] == id) {
                    this.state.highestgrade.splice(i, 1)
                    break
                }
            }

        }

    }

    /**
     * Mtodo para gerenciar os checkboxes relacionados a Ensino Profissionalizante (Curso Tcnico).
     */
    handleTechCourseCheckbox = value => (event) => {

        if (event.target.checked) {
            this.state.techcourse.push(value)
        } else {

            for (let i = 0; i < this.state.techcourse.length; i++) {
                if (this.state.techcourse[i] == value) {
                    this.state.techcourse.splice(i, 1)
                    break
                }
            }

        }

    }

    /**
     * Mtodo para gerenciar os checkboxes relacionados a Experincia Profissional.
     */
    handleProfExperienceCheckbox = value => (event) => {

        if (event.target.checked) {
            this.state.profexperience.push(value)
        } else {

            for (let i = 0; i < this.state.profexperience.length; i++) {
                if (this.state.profexperience[i] == value) {
                    this.state.profexperience.splice(i, 1)
                    break
                }
            }

        }

    }

    /**
     * Mtodo para gerenciar o texto do Cid10.
     */
    handleCidTextfield = value => (event) => {

        this.setState({cid: event.target.value})
    }

    /**
     * Mtodo auxiliar para filtrar queries do Firestore de forma
     * dinmica, ou seja, com parmetro de buscas diferentes.
     *
     * ATENO! Esse mtodo no  o mais otimizado para o servidor devido
     * a falta de operadores lgicos para a busca no Firestore!  recomendvel
     * tentar buscar outro meio de filtro!!!
     */
    filterResult(doc) {

        let canShow = true

        if (!this.state.gender.includes(doc.data().gender.toLowerCase()) && this.state.gender.length > 0) {
            canShow = false
        } else if (!this.state.highestgrade.includes(parseInt(doc.data().highestgrade)) && this.state.highestgrade.length > 0) {
            canShow = false
        } else if (!this.state.profexperience.includes(doc.data().profexperience) && this.state.profexperience.length > 0) {
            canShow = false
        } else if (!this.state.techcourse.includes(doc.data().techcourse) && this.state.techcourse.length > 0) {
            canShow = false
        } else if (this.state.cid != "") {

            if (doc.data().cid == "" || doc.data().cid == null) {
                canShow = false
            } else if (this.state.cid.toLowerCase() != doc.data().cid.toLowerCase()) {
                canShow = false
            }
        }

        return canShow
    }

    /**
     * Mtodo para carregar todas as informaes do Firebase's Firestore
     * de acordo com a preferncia do usurio.
     */
    renderData() {

        // Lembrando que se showResult (mudou para tela de resultados)
        // e willShowData (o banco de dados vai mostrar dados)... Os
        // dados devero ser tratados!
        if (this.props.showResult && this.state.willShowData) {

            // Se h algo armazenado no "buffer" rows... Limpando tudo...
            if (this.state.rows.length > 0) {
                this.setState({rows: []})
            }


            firebase.firestore.collection("accounts").where("role", "==", "student-enabled").get().then((queryResults) => {

                let rows = []

                queryResults.forEach((doc) => {
                    if (this.filterResult(doc)) {

                        let toConvertData = doc.data()

                        // Padronizando gneros (deixando tudo no diminutivo)
                        toConvertData.gender = toConvertData.gender.toLowerCase()

                        // Convertendo "Maior Grau Completo" de nmero para string
                        switch (toConvertData.highestgrade) {
                            case 0:
                                toConvertData.highestgrade = "Indefinido"
                                break;
                            case 1:
                                toConvertData.highestgrade = "Ensino Fundamental Incompleto"
                                break;
                            case 2:
                                toConvertData.highestgrade = "Ensino Fundamental Completo"
                                break;
                            case 3:
                                toConvertData.highestgrade = "Ensino Mdio Incompleto"
                                break;
                            case 4:
                                toConvertData.highestgrade = "Ensino Mdio Completo"
                                break;
                            case 5:
                                toConvertData.highestgrade = "Ensino Superior Incompleto"
                                break;
                            case 6:
                                toConvertData.highestgrade = "Ensino Superior Completo"
                                break;
                            default:
                                toConvertData.highestgrade = "?"
                        }

                        // Convertendo Experincia Profissional de booleano para string
                        if (toConvertData.profexperience == true) {
                            toConvertData.profexperience = "Sim"
                        } else {
                            toConvertData.profexperience = "No"
                        }

                        // Convertendo Ensino Tcnico de booleano para string
                        if (toConvertData.techcourse == true) {
                            toConvertData.techcourse = "Sim"
                        } else {
                            toConvertData.techcourse = "No"
                        }

                        // Tratando informao sobre o CID vazias...
                        if (toConvertData.cid == "" || toConvertData.cid == null) {
                            toConvertData.cid = "?"
                        }

                        // Tratando informao sobre DDN vazias...
                        if (toConvertData.birthdate == "" || toConvertData.birthdate == null) {
                            toConvertData.birthdate = "?"
                        }


                        rows.push(this.addCellData(toConvertData.name, toConvertData.gender, toConvertData.birthdate, toConvertData.highestgrade,
                            toConvertData.techcourse, toConvertData.profexperience, toConvertData.cid, toConvertData.address, toConvertData.city,
                            toConvertData.phone, toConvertData.email, "?"))

                    }
                })

                // Limpando todos os parmetros de busca...
                // E tambm diremos que no precisaremos obter mais nada do banco de dados
                this.setState({
                    gender: [],
                    highestgrade: [],
                    techcourse: [],
                    profexperience: [],
                    cid: "",
                    willShowData: false,
                    rows: rows
                })

            })
                .catch(err => {
                    console.log("[ERROR] Database is not available or the connection was refused!")
                })
        }

    }

    render() {

        // Chamando mtodo de validao de contedo.
        // Caso seja necessrio buscar novas informaes no banco de dados...
        // Os dados sero reamostrados na tela.
        this.renderData()

        // Renderizando componente...
        return (
            <Paper
                className={this.state.classes.root}
                component={Box}
                margin='auto'
                maxWidth={1920}
            >
                <AppBar
                    component={Box}
                    borderBottom='1px solid rgba(0, 0, 0, 0.12)'
                    position='static'
                    color='default'
                    maxWidth={1920}
                    elevation={0}
                    padding={2}>

                    <Grid
                        alignItems='left'
                        container>

                        {/* TTULO - Buscar Currculos */}
                        <Grid item xs={6}>
                            <Typography color='textPrimary' align='left' variant='h4' component='h4'>
                                Buscar Currculos
                            </Typography>
                        </Grid>

                        {/* Tweakzinho para ajustar o boto de Nova Consulta no GRID */}
                        {this.props.showResult ? (
                            <Grid item xs={4}></Grid>) : null}

                        {/* Boto de Nova Consulta */}
                        {this.props.showResult ? (
                            <Grid item xs={2}>
                                <Box mt={1}>
                                    <Button
                                        className={this.state.classes.button}
                                        color='primary'
                                        variant='contained'
                                        onClick={this.props.changeFormState}
                                    >
                                        Nova consulta
                                    </Button>
                                </Box>
                            </Grid>) : null}

                    </Grid>
                </AppBar>


                {this.props.showResult ?
                    (
                        /* Demonstrando uma lista de resultados do Banco de Dados */
                        <Grid
                            alignItems='left'
                            container
                            direction="column">
                            <Box padding={2}>
                                ltda
                                {/* Table View */}
                                <Grid item>
                                    <Table>
                                        <TableHead>
                                            <TableRow>
                                                <TableCell>Nome</TableCell>
                                                <TableCell>Gnero</TableCell>
                                                <TableCell>Data de Nascimento</TableCell>
                                                <TableCell>Grau de Instruo</TableCell>
                                                <TableCell>Ensino Profissionalizante</TableCell>
                                                <TableCell>Experincia Profissional</TableCell>
                                                <TableCell>CID</TableCell>
                                                <TableCell>Informao</TableCell>
                                            </TableRow>
                                        </TableHead>

                                        <TableBody>

                                            {this.state.willShowData ? null : (
                                                this.state.rows.map(row => (
                                                    <CurriculumData name={row.name} gender={row.gender}
                                                                    birthdate={row.birthdate}
                                                                    highestgrade={row.highestgrade}
                                                                    profexperience={row.profexperience}
                                                                    techcourse={row.techcourse} cid={row.cid}
                                                                    address={row.address}
                                                                    city={row.city} phone={row.phone}
                                                                    email={row.email}/>
                                                )))}

                                        </TableBody>
                                    </Table>
                                </Grid>
                            </Box>
                        </Grid>
                    ) :
                    (
                        /* Demonstrando um formulrio de busca */

                        <Grid
                            alignItems='left'
                            container
                            direction="column">

                            <Box padding={2}>

                                {/* Gnero */}
                                <Grid item>
                                    <Box mt={3}>
                                        <FormControl component="fieldset">
                                            <Box mb={2}>
                                                <Typography color='textPrimary' align='left' variant='h4'
                                                            component='h4'>
                                                    Gnero:
                                                </Typography>
                                            </Box>
                                            <Box ml={1}>
                                                <FormGroup>
                                                    <FormControlLabel
                                                        control={<Checkbox value="Masculino"
                                                                           onChange={this.handleGenderCheckbox()}/>}
                                                        label="Masculino"
                                                    />
                                                    <FormControlLabel
                                                        control={<Checkbox value="Feminino"
                                                                           onChange={this.handleGenderCheckbox()}/>}
                                                        label="Feminino"
                                                    />
                                                    <FormControlLabel
                                                        control={<Checkbox value="Indefinido"
                                                                           onChange={this.handleGenderCheckbox()}/>}
                                                        label="Indefinido"
                                                    />
                                                </FormGroup>
                                            </Box>
                                        </FormControl>
                                    </Box>
                                </Grid>

                                {/* Grau de Instruo */}
                                <Grid item>
                                    <Box mt={5}>
                                        <FormControl component="fieldset">
                                            <Box mb={2}>
                                                <Typography color='textPrimary' align='left' variant='h4'
                                                            component='h4'>
                                                    Grau de Instruo:
                                                </Typography>
                                            </Box>
                                            <Box ml={1}>
                                                <FormGroup>


                                                    <Grid container>

                                                        {/* Coluna da Esquerda */}

                                                        <Grid item xs={4}>
                                                            <FormControlLabel
                                                                control={<Checkbox value="Ensino Fundamental Incompleto"
                                                                                   onChange={this.handleHighestGradeCheckbox(1)}/>}
                                                                label="Ensino Fundamental Incompleto"
                                                            />
                                                            <FormControlLabel
                                                                control={<Checkbox value="Ensino Fundamental Completo"
                                                                                   onChange={this.handleHighestGradeCheckbox(2)}/>}
                                                                label="Ensino Fundamental Completo"
                                                            />
                                                            <FormControlLabel
                                                                control={<Checkbox value="Ensino Mdio Incompleto"
                                                                                   onChange={this.handleHighestGradeCheckbox(3)}/>}
                                                                label="Ensino Mdio Incompleto"
                                                            />
                                                            <FormControlLabel
                                                                control={<Checkbox value="Ensino Mdio Completo"
                                                                                   onChange={this.handleHighestGradeCheckbox(4)}/>}
                                                                label="Ensino Mdio Completo"
                                                            />
                                                        </Grid>

                                                        {/* Coluna da Direita */}

                                                        <Grid item xs={3}>
                                                            <Box ml={4}>
                                                                <FormControlLabel
                                                                    control={<Checkbox
                                                                        value="Ensino Superior Incompleto"
                                                                        onChange={this.handleHighestGradeCheckbox(5)}/>}
                                                                    label="Ensino Superior Incompleto"
                                                                />
                                                                <FormControlLabel
                                                                    control={<Checkbox value="Ensino Superior Completo"
                                                                                       onChange={this.handleHighestGradeCheckbox(6)}/>}
                                                                    label="Ensino Superior Completo"
                                                                />
                                                                <FormControlLabel
                                                                    control={<Checkbox value="Indefinido"
                                                                                       onChange={this.handleHighestGradeCheckbox(0)}/>}
                                                                    label="Indefinido"
                                                                />
                                                            </Box>
                                                        </Grid>

                                                    </Grid>

                                                </FormGroup>
                                            </Box>
                                        </FormControl>
                                    </Box>
                                </Grid>

                                {/* Ensino Profissionalizante */}
                                <Grid item>
                                    <Box mt={5}>
                                        <FormControl component="fieldset">
                                            <Box mb={2}>
                                                <Typography color='textPrimary' align='left' variant='h4'
                                                            component='h4'>
                                                    Ensino Profissionalizante:
                                                </Typography>
                                            </Box>
                                            <Box ml={1}>
                                                <FormGroup>
                                                    <FormControlLabel
                                                        control={<Checkbox value="Sim"
                                                                           onChange={this.handleTechCourseCheckbox(true)}/>}
                                                        label="Sim"
                                                    />
                                                    <FormControlLabel
                                                        control={<Checkbox value="No"
                                                                           onChange={this.handleTechCourseCheckbox(false)}/>}
                                                        label="No"
                                                    />
                                                </FormGroup>
                                            </Box>
                                        </FormControl>
                                    </Box>
                                </Grid>

                                {/* Experincia Profissional */}
                                <Grid item>
                                    <Box mt={5}>
                                        <FormControl component="fieldset">
                                            <Box mb={2}>
                                                <Typography color='textPrimary' align='left' variant='h4'
                                                            component='h4'>
                                                    Experincia Profissional:
                                                </Typography>
                                            </Box>
                                            <Box ml={1}>
                                                <FormGroup>
                                                    <FormControlLabel
                                                        control={<Checkbox value="Sim"
                                                                           onChange={this.handleProfExperienceCheckbox(true)}/>}
                                                        label="Sim"
                                                    />
                                                    <FormControlLabel
                                                        control={<Checkbox value="No"
                                                                           onChange={this.handleProfExperienceCheckbox(false)}/>}
                                                        label="No"
                                                    />
                                                </FormGroup>
                                            </Box>
                                        </FormControl>
                                    </Box>
                                </Grid>

                                {/* CID */}
                                <Grid item>
                                    <Box mt={5}>
                                        <FormControl component="fieldset">
                                            <Box mb={2}>
                                                <Typography color='textPrimary' align='left' variant='h4'
                                                            component='h4'>
                                                    CID (Opcional):
                                                </Typography>
                                                <TextField
                                                    label="Digite o CID aqui"
                                                    margin="normal"
                                                    fullWidth="true"
                                                    onChange={this.handleCidTextfield()}
                                                />
                                            </Box>
                                        </FormControl>
                                    </Box>
                                </Grid>

                                {/* Boto de busca */}
                                <Grid item>
                                    <Box mt={5} mb={1}>
                                        <Button
                                            className={this.state.classes.button}
                                            color='primary'
                                            variant='contained'
                                            fullWidth='true'
                                            onClick={() => this.resetData()}
                                        >
                                            Buscar
                                        </Button>
                                    </Box>

                                </Grid>
                            </Box>
                        </Grid>
                    )}


            </Paper>
        )
    }

}

const mapStateToProps = ({firestore}) => ({firestore})

const mapDispatchToProps = dispatch => ({
    getContent: type => dispatch(getContent({type})),
    openSnackbar: options => dispatch(openSnackbar(options)),
    filterContent: query => dispatch(filterContent(query)),
    filterContentSuccess: () => dispatch(filterContentSuccess()),
    deleteContent: (uid, options) => dispatch(deleteContent(uid, options)),
    openForm: options => dispatch(openForm(options)),
})

export default compose(
    withStyles(styles),
    connect(mapStateToProps, mapDispatchToProps),
)(CurriculumPage);